<template>
    <div>
        <el-container>
            <!-- 侧边栏 -->
            <el-aside :width="asideWidth" style=" min-height:100vh; background-color: #001529;">
              <div style="height: 60px; color: white; display: flex; align-items:center; justify-content:center;">
                  <img src="@/assets/youon.png" alt="" style="width: 36px; height: 36px;">
                  <!-- <transition name="el-fade-in-linear"> 
                      <span style="margin-left: 5px; font-size: 20px;" v-show="!isCollapse">TTQ2024</span>
                  </transition> -->
                  <span class="logotitle"  v-show="!isCollapse">云桑Youon</span>
              </div>

              <el-menu :collapse="isCollapse" :collapse-transition="false" router background-color="#001529" text-color="rgba(255,255,255,0.65)" active-text-color="#fff" style="border:none;" :default-active="$route.path">
                  <el-menu-item index="/">
                      <i class="el-icon-house"></i>
                      <span slot="title">系统首页</span>
                  </el-menu-item>
                  <el-menu-item index="/element">
                      <i class="el-icon-s-grid"></i>
                      <span slot="title">常用工具</span>
                  </el-menu-item>
                  <el-submenu index="/progremview">
                    <template slot="title"><i class="el-icon-mobile-phone"></i><span>组件视图</span></template>
                    <el-menu-item index="/kbsview">
                    <span slot="title">卡博士</span>
                    </el-menu-item>
                    <el-menu-item index="/ysbzview">
                    <span slot="title">云桑壁纸</span>
                    </el-menu-item>
                </el-submenu>
                  <el-menu-item index="/staricon">
                      <i class="el-icon-star-off"></i>
                      <span slot="title">收藏网站</span>
                  </el-menu-item>
                  <el-menu-item index="/userlist">
                      <i class="el-icon-s-custom"></i>
                      <span slot="title">用户列表</span>
                  </el-menu-item>

                  <el-submenu index="/message">
                      <template slot="title"><i class="el-icon-menu"></i><span>信息管理</span></template>
                      <el-menu-item index="/messages/list">
                          <span slot="title">信息列表</span>
                      </el-menu-item>
                      <el-menu-item index="/messages/admin">
                          <span slot="title">管理后台</span>
                      </el-menu-item>
                  </el-submenu>
              </el-menu>

          </el-aside>

            <el-container>
                <!-- 右边的头部 -->
                <el-header>
                    <i :class="collapseIcon" style="font-size: 26px; margin-right: 10px;" @click="handleCollapse"></i>
                    <el-breadcrumb separator-class="el-icon-arrow-right" style="margin-left: 20px;">
                        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
                        <el-breadcrumb-item >用户信息</el-breadcrumb-item>
                    </el-breadcrumb>

                    <div style="flex: 1; width: 0; display: flex; align-items: center; justify-content: flex-end;">
                        <i class="el-icon-full-screen" style="font-size: 26px; margin:0 5px;" @click="handleFull"></i>
                        <el-dropdown placement="bottom">
                            <!-- flex居中,cursor更改鼠标悬停文字样式为箭头 -->
                            <div style="display: flex; align-items: center; cursor: default;">
                                <img src="@/assets/logo2.png" alt="" style="width: 40px; height: 40px; margin-right: 5px;">
                                <span>管理员</span>
                            </div>

                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item>个人信息</el-dropdown-item>
                                <el-dropdown-item>修改密码</el-dropdown-item>
                                <el-dropdown-item>
                                <router-link to="/about">关于我们</router-link>
                                </el-dropdown-item>
                                <el-dropdown-item divided >
                                <router-link to="/login">退出登录</router-link>
                                </el-dropdown-item>
                            </el-dropdown-menu>
                        </el-dropdown>
                    </div>




                </el-header>

                <!-- 主体 -->
                <el-main>
                    
                   <div class="search_head_container" style="display: flex; margin: 30px 0;">
                        <el-input placeholder="请输入关键字" v-model="input" class="input-with-select" style="width: 300px;" >
                            <el-button type="primary" slot="append" icon="el-icon-search">搜索</el-button>
                        </el-input>
                        <el-button type="info" style="margin-left: 20px;">重置</el-button>
                   </div>

                    <div class="btn_container">
                        <el-button type="primary" disabled>导出</el-button>
                        <el-button type="success"  @click="toggleSelection(tableData)">全选</el-button>
                        <el-button type="warning" @click="toggleSelection()">取消</el-button>
                        <el-button type="danger" disabled>删除</el-button>
                    </div>
                    <div class="user_list_container">
                        <el-table
                            ref="multipleTable"
                            :data="tableData"
                            stripe
                            tooltip-effect="dark"
                            style="width: 100%"
                            @selection-change="handleSelectionChange">
                            <el-table-column
                                type="selection"
                                width="55">
                            </el-table-column>
                            <el-table-column
                                label="日期"
                                width="120">
                            <template slot-scope="scope">{{ scope.row.date }}</template>
                            </el-table-column>
                            <el-table-column
                                prop="name"
                                label="姓名"
                                width="120">
                            </el-table-column>
                            <el-table-column
                                prop="address"
                                label="地址"
                                show-overflow-tooltip>
                            </el-table-column>
                        </el-table>
                    </div>
                    
                </el-main>
                <!-- 主体 -->
            </el-container>


        </el-container>
    </div>
</template>

<script>
export default {
    name:'Element',
    data(){
        return{
            searchValue:'',
            isCollapse: false ,//默认不收缩
            asideWidth:"200px",
            collapseIcon:"el-icon-s-fold",
            tableData: [{
            date: '2016-05-03',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1518 弄'
            }, {
            date: '2016-05-02',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1518 弄'
            }, {
            date: '2016-05-04',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1518 弄'
            }, {
            date: '2016-05-01',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1518 弄'
            }, {
            date: '2016-05-08',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1518 弄'
            }, {
            date: '2016-05-06',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1518 弄'
            }, {
            date: '2016-05-07',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1518 弄'
            }],
            multipleSelection: []
        }
    },
    methods:{
        handleFull(){
            // 全屏
            document.documentElement.requestFullscreen();
        },
        handleCollapse(){
            this.isCollapse =! this.isCollapse;
            this.asideWidth = this.isCollapse ? "64px" : "200px";
            this.collapseIcon = this.isCollapse ? "el-icon-s-unfold" : "el-icon-s-fold";

        },
        toggleSelection(rows) {
            if (rows) {
                this.$refs.multipleTable.clearSelection();
                rows.forEach(row => {
                    this.$refs.multipleTable.toggleRowSelection(row);
                });
            } else {
            this.$refs.multipleTable.clearSelection();
            }
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        }
    }
}
</script>


<style scoped>
.btn_container{
    display: flex; 
    justify-content: end; 
    align-items: center; 
    background-color: rgb(238, 238, 238); 
    box-shadow: 0 0 10px rgba(0,0,0,.1); 
    padding: 5px 20px; 
    border-radius: 5px 5px 0 0; 
}
</style>